import { dbGenerators } from "../../../database"
import { generateAgentChatMessages } from "../../database/generators/generateAgentChat"
import { AgentChatResponseHandler } from "./agent-handlers.types"

const defaultResearchPrompt: AgentChatResponseHandler = async ({ inputMessage, requestId }) => {
  const content = "Bigdata is a powerful tool for analyzing large datasets."
  const interaction = dbGenerators.generateChatInteraction({
    inputMessage,
    content,
  })
  const messages = generateAgentChatMessages({ requestId, content })

  return {
    messages: [
      messages.thinking("thinking1", "Let me search"),
      messages.thinking("thinking1", " for Bigdata on the web"),
      messages.action("action1", "SEARCH"),
      messages.actionAudit("action1", "SEARCH", {
        query: "What is Bigdata?",
        documents: [{ hd: "Bigdata is cool" }],
      }),
      messages.actionAudit("action1", "SEARCH", {
        query: "What kind of platform Bigdata is?",
        documents: [{ hd: "Bigdata is an AI platform" }],
      }),
      ...messages.answer(),
      messages.complete(),
    ],
    interaction,
  }
}

export { defaultResearchPrompt }
